import React, { Component } from 'react';

class Portfolio extends Component {
    render() {
        const { data, displayData } = this.props;

        const dataList = data.map((item, i) => {
            return (
                <div className='col-md-4 portfolio-items'
                    id={item.id}
                    key={i}
                    info={item}
                    onClick={() => displayData(item)}
                >
                    <div className='img-box'>
                        <img src={item.image} alt="" />
                    </div>
                    <div className='portfolio-bottom'>
                        <h4>{item.name}</h4>
                        <p className='caption'>{item.caption}</p>
                    </div>
                    <div className='hidden-desktop'><p>{item.info}</p></div>
                </div>
            )
        })

        return (
            <div id='portfolio' >
                <div className='container portfolio'>
                    <h1>Portfolio</h1>
                    <p className='portfolio-info'>Min portfolio innehller inte s mnga projekt fr tillfllet. Brjade studera frontend hsten 2017, och knappt ett r senare bygger jag min portfolio sjlv i React. r faktiskt lite imponerad och stolt ver mig sjlv. Mina allra frsta projekt hittar du lngst ner p listan, det r ganska s stor skillnad p kvalitet och framfrallt hastigheten jag bygger i nu. Fler hopp- och lekprojekt finns p Github. Har ngra pgende projekt p min praktikplats just nu ocks.</p>
                </div >
                <div className='row top-level container'>
                    {dataList}
                </div>
            </div>
        );
    }
}

export default Portfolio;



