import React, { Component } from 'react';
import Header from './header';
import Footer from './footer';

class About extends Component {
    render() {
        return (
            <div className='about'>
                <div className='footer-adj'>
                    <Header />

                    <div className='container-fluid'>
                        <img src="/myportfolio/images/about.jpg" alt="" />
                    </div>

                    <div className='container'>
                        <h1>Om mig</h1>
                        <p className='about-info'>
                            Hr r jag, en fartfylld och hrlig tjej som kallar sig fr Madde. Pbrjade min
                                arbetskarrir i unga tonren r inom restaurang. Efter gymnasiet fick jag jobb p
                                ett husfretag som receptionist/sljstd fr utesljarna. Ganska snabbt
                                utkades det med strre ansvar som att gra kontraktshandlingar, bestlla ritningar,
                                se till att kundernas val var inne i rtt tid, hjlpa sljarna med offerter, ta fram
                                tillval, inkl. kataloger och prislistor. De sista ren arbetade jag mestadels med
                                kalkylering. Efter det har jag arbetat som projektledare och kalkylator fr undertak.
                        <br /><br />
                            Efter mina 12 r i byggbranschen och ngra smbarnsr hemma beslutade jag mig fr att
                                jag skulle gra ngot annat. Jag hamnade p Mobil Handel vid Skne Yrkeshgskola, en
                                frontend-utbilning med inriktning p Mobil Handel. Jag stormtrivs med att koda och ngrar
                                mig inte fr en sekund. Det finns s mycket att lra sig och jag stter dagligen p nya
                                saker att utforska. Just den hr hemsidan r mitt frsta mte med bootstrap, herregud s
                                smidigt!
                        <br /><br />
                            Sammanfattningsvis r jag en tjej med skinn p nsan, lskar att lra, ser grna utanfr
                                boxen och rds inte av att ta i och gra ett ordentligt jobb.
                        <br /><br />



                            r gift och har 2 barn, pojkar. Vi bor i ett hus p landet mellan Enkping
                            och Vsters med skog och krar som
                            grannar i ett hus som vi sjlva byggt fr snart tio r sen. Innan barnen var vi
                            flitiga i garaget, det intresset har av naturliga skl svalnat. Fr tillfllet
                            sitter jag oftast vid datorn och labbar nr barnen somnat, annars spenderar jag
                        en del tid i trdgrden och vxthuset.  <br /><br />


                        </p>
                        <p className='sign'>Madelein Skoglund</p>
                    </div>
                    <Footer />
                </div >
            </div >
        )
    }
}

export default About;



